import { Component, OnInit, Injector } from '@angular/core';
import { UtilityTrackerService } from 'src/app/api/controllers/UtilityTracker';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';
import { ModalService } from 'src/app/shared/services/modal.service';

@Component({
  selector: 'app-utility-add-reading',
  templateUrl: './utility-add-reading.component.html',
  styleUrls: ['./utility-add-reading.component.scss']
})
export class UtilityAddReadingComponent implements OnInit {

  utilityCategoryData: any;
  apartment_id: number;
  isUtilityCategoryLoaded: boolean = false;
  selected_utility_type: boolean = false;
  utility_selected_category: any;
  utilityReading: any;
  selected_index: any;

  modalService: any;


  public params = {
    apartmentId: this.sessionService.apartmentId
  }
  constructor(private injector: Injector,
    private utilityTrackerService: UtilityTrackerService,
    private lookupService: LookupService,
    private sharedService: SharedService,
    private sessionService: SessionService
  ) {
    this.modalService = this.injector.get(ModalService);
  }

  loadUtilityCategory() {
    let params = {
      apartmentId: this.sessionService.apartmentId
    }
    this.utilityTrackerService.getUtilityTrackerCategorysByApartmentId(params).subscribe((res: any) => {
      this.utilityCategoryData = res.filter(item => {
        return item.isActive;
      });
      this.utilityCategoryData.filter(key => {
        if (key.utilityTrackerCategoryId == 11) {
          key['url'] = "electricity-reading";
        }
        else if (key.utilityTrackerCategoryId == 12) {
          key['url'] = "water-reading";
        }
        else if (key.utilityTrackerCategoryId == 14) {
          key['url'] = "insurance";
        }
      })
      console.log('log', this.utilityCategoryData)
      this.isUtilityCategoryLoaded = true;
    },
      error => {
      });
  }

  onSelectCat(i) {
    this.selected_index = i;
  }

  onCancel(e) {
    this.selected_utility_type = false;
  }

  selectUtility(category) {
    this.selected_utility_type = true;
    this.utility_selected_category = category;
  }

  ngOnInit(): void {
    this.apartment_id = this.sessionService.apartmentId
    this.loadUtilityCategory()
  }

}
