import { Component, OnInit, Injector, ViewChild } from '@angular/core';
import { UtilityTrackerService } from 'src/app/api/controllers/UtilityTracker';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';
import { ApartmentService } from 'src/app/api/controllers/Apartment';

import { ModalService } from 'src/app/shared/services/modal.service';

import * as _ from 'underscore';
import * as moment from 'moment';
import _moment from "moment-timezone";
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { ConstantsService } from 'src/app/shared/services/constants.service';

@Component({
  selector: 'app-utility-history',
  templateUrl: './utility-history.component.html',
  styleUrls: ['./utility-history.component.scss']
})
export class UtilityHistoryComponent implements OnInit {

  utilityCategoryData: string = "";

  isUtilityCategoryLoaded: boolean = false;
  isUltilityElectricHisLoaded: boolean = false;
  isUltilityWaterHisLoaded: boolean = false;
  isUltilityInsuranceHisLoaded: boolean = false;
  selectedUtilityCategory: any;
  filteredUtilityWaterListData: any;
  propertyTowerData: any;
  selectedTower: any;


  utilitySearch: string = "";

  utilityElecteicalListPeriods: any;

  apartment_id: number;
  utilityElecteicalListData: any;
  utilityWaterListData: any;
  utilityInsuranceListData: any;

  unitFieldType: string = "utilityName";
  unitOrder: boolean = true;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 10;
  totalFilterItems: any;

  selectedInput: string = "";
  columnField: any = {}

  modalService: any;

  utilityElecteicalHeader: Array<any> = [];
  filteredUtilityHeader: Array<any> = [];
  utilityInsuranceHeader: Array<any> = [];

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  timeZone: any;

  public params = {
    apartmentId: this.sessionService.apartmentId
  }
  constructor(
    private lookupService: LookupService,
    private sharedService: SharedService,
    private apartmentService: ApartmentService,
    private utilityTrackerService: UtilityTrackerService,
    private sessionService: SessionService,
    private constantsService: ConstantsService
  ) {

  }

  getIndexParams(event) {
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type) {
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if (this.unitFieldType == type) {
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  selectColInput(value) {
    this.selectedInput = value;
  }

  isWaterHistory() {
    if (this.selectedUtilityCategory == 'Water Meter Charges') {
      return true;
    } else {
      return false;
    }
  }

  onFilterByTowerChange() {
    if (this.selectedTower == 'All') {
      this.filteredUtilityWaterListData = this.utilityWaterListData;
    } else {
      this.filteredUtilityWaterListData = this.utilityWaterListData.filter(x => x.block == this.selectedTower);
    }
    this.totalItems = this.filteredUtilityWaterListData.length;
  }

  onUtilityCatregoryChange() {
    this.ItemStartIndex = 0;


    if (this.selectedUtilityCategory == 'Common Electricity Charges') {
      this.isUltilityWaterHisLoaded = false;
      this.isUltilityInsuranceHisLoaded = false;
      this.getAllElectricalUtiliy();

    } else if (this.selectedUtilityCategory == 'Water Meter Charges') {
      this.isUltilityElectricHisLoaded = false;
      this.isUltilityInsuranceHisLoaded = false;
      this.getAllWaterUtiliy();

    } else if (this.selectedUtilityCategory == 'Common Insurance') {
      this.isUltilityWaterHisLoaded = false;
      this.isUltilityElectricHisLoaded = false;
      this.getAllInsuranceUtiliy();

    } else if (this.selectedUtilityCategory == 'undefined') {
      this.selectedUtilityCategory = undefined;
    }
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isNoItemsAvailable() {
    return this.totalItems == 0 ? true : false;
  }

  getUtilityFromPeriod(period_id) {
    let electeicalListPeriods = this.utilityElecteicalListPeriods.find(x => x.period_id == period_id);
    if (electeicalListPeriods) {
      return electeicalListPeriods.periods[0].periodFrom.slice(0, 10)
    }
  }

  getUtilityToPeriod(period_id) {
    let electeicalListPeriods = this.utilityElecteicalListPeriods.find(x => x.period_id == period_id);
    if (electeicalListPeriods) {
      return electeicalListPeriods.periods[0].periodTo.slice(0, 10)
    }
  }


  getAllElectricalUtiliy() {
    this.isUltilityElectricHisLoaded = false;
    let params = {
      apartmentId: this.sessionService.apartmentId
    }
    this.utilityTrackerService.getElectricityChargesByApartmentId(params).subscribe((res: any) => {
      let utilityElecteicalList = res.filter(item => {
        return item.isActive;
      });
      let utilityElecteicalSourceData = {
        localdata: utilityElecteicalList,
        datatype: "array"
      }
      this.totalItems = utilityElecteicalList.length;
      this.utilityElecteicalListData = new jqx.dataAdapter(utilityElecteicalSourceData);
      // this.utilityElecteicalListPeriods = []
      // for (let electrical_list of this.utilityElecteicalListData) {
      //   let period_param = { "apartmentId": this.sessionService.apartmentId, "utilityTrackerElectricityPeriodId": electrical_list.utilityTrackerElectricityPeriodId }
      //   this.utilityTrackerService.getUtilityTrackerElectricityPeriodsById(period_param).subscribe((res: any) => {

      //     let period_data = { "periods": res, "period_id": electrical_list.utilityTrackerElectricityPeriodId }
      //     this.utilityElecteicalListPeriods.push(period_data)
      //   })
      // }

      // this.totalItems = this.utilityElecteicalListData.length;

      // if (this.totalItems > this.itemLimit) {
      //   this.ItemEndIndex = this.itemLimit;
      // }
      // else {
      //   this.ItemEndIndex = this.totalItems;
      // }

      this.isUltilityElectricHisLoaded = true;

    });

  }


  getAllWaterUtiliy() {
    this.selectedTower = 'All';
    this.isUltilityWaterHisLoaded = false;
    let params = {
      apartmentId: this.sessionService.apartmentId
    }
    this.utilityTrackerService.getUtilityTrackerWaterChargesByApartmentId(params).subscribe((res: any) => {

      let filteredUtilityWaterList = res;
      let filteredUtilityWaterSourceData = {
        localdata: filteredUtilityWaterList,
        datatype: "array"
      }
      
      this.filteredUtilityWaterListData = new jqx.dataAdapter(filteredUtilityWaterSourceData);
      this.totalItems = filteredUtilityWaterList.length;
      this.isUltilityWaterHisLoaded = true;

    });

  }


  getAllInsuranceUtiliy() {
    this.isUltilityInsuranceHisLoaded = false;
    let params = {
      apartmentId: this.sessionService.apartmentId
    }
    this.utilityTrackerService.getUtilityTrackerInsuranceChargesByApartmentId(params).subscribe((res: any) => {

      let utilityInsuranceList = res.filter(item => {
        return item.isActive;
      });
      this.totalItems = utilityInsuranceList.length;
      let utilityInsuranceSourceData = {
        localdata: utilityInsuranceList,
        datatype: "array"
      }

      this.utilityInsuranceListData = new jqx.dataAdapter(utilityInsuranceSourceData);
      // this.utilityInsuranceListData.sort(function (a, b) {
      //   a = new Date(a.dateEntered);
      //   b = new Date(b.dateEntered);
      //   return a > b ? -1 : a < b ? 1 : 0;
      // });
      // this.totalItems = this.utilityInsuranceListData.length;

      // if (this.totalItems > this.itemLimit) {
      //   this.ItemEndIndex = this.itemLimit;
      // }
      // else {
      //   this.ItemEndIndex = this.totalItems;
      // }

      this.isUltilityInsuranceHisLoaded = true;

    });

  }

  onGlSearchFilter(event) {
    if (event != "") {
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = event;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      if (this.selectedUtilityCategory == 'Common Electricity Charges') {
        this.utilityElecteicalHeader.forEach(item => {
          if (item.datafield != 'Actions') {
            this.datagrid.addfilter(item.datafield, filtergroup, true);
          }
        })
      } else if (this.selectedUtilityCategory == 'Water Meter Charges') {
        this.filteredUtilityHeader.forEach(item => {
          if (item.datafield != 'Actions') {
            this.datagrid.addfilter(item.datafield, filtergroup, true);
          }
        })
      } else {
        this.utilityInsuranceHeader.forEach(item => {
          if (item.datafield != 'Actions') {
            this.datagrid.addfilter(item.datafield, filtergroup, true);
          }
        })
      }
      this.datagrid.applyfilters();
    }
    else {
      this.datagrid.clearfilters();
    }
  }



  ngOnInit() {

    this.sharedService.timezonecast.subscribe(timeZone => this.timeZone = timeZone)

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.utilityElecteicalHeader = [
      {
        text: 'Condo unit',
        datafield: 'apartmentUnitCount',
        width: 250,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'from',
        datafield: 'periodFrom',
        width: 200,
        cellsrenderer: (row, column, value): string => {
          return '<div class="jqx-custom-inner-cell">' + moment(value).format(this.timeZone.date) + '</div>'
        },
        renderer: columnrenderer
      },
      {
        text: 'to',
        datafield: 'periodTo',
        width: 200,
        cellsrenderer: (row, column, value): string => {
          return '<div class="jqx-custom-inner-cell">' + moment(value).format(this.timeZone.date) + '</div>'
        },
        renderer: columnrenderer
      },
      {
        text: 'Pre Reading',
        datafield: 'prevReading',
        width: 250,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'Curr Reading',
        datafield: 'currReading',
        width: 250,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'Cost Per Unit',
        datafield: 'costPerAptUnit',
        width: 200,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'Added By',
        datafield: 'insertedByUser',
        width: 200,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      }
    ]

    //TODO array of object assign 
    this.filteredUtilityHeader = [
      {
        text: 'Condo Unit',
        datafield: 'aptUnit',
        width: 250,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'Tower',
        datafield: 'block',
        width: 200,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'Connection No',
        width: 250,
        cellsrenderer:  (row, column, value) => {
          var content = this.filteredUtilityWaterListData.records[row].waterCharge.length > 0 ? this.filteredUtilityWaterListData.records[row].waterCharge[0]["connectionNo"] : ''
          return '<div class="jqx-custom-inner-cell">' +  content + '</div>';
        },
        renderer: columnrenderer
      },
      {
        text: 'Previous Reading',
        width: 250,
        cellsrenderer: (row, column, value): string => {
          var content = this.filteredUtilityWaterListData.records[row].waterCharge.length > 0 ? this.filteredUtilityWaterListData.records[row].waterCharge[0]["prevReading"] : ''
          return '<div class="jqx-custom-inner-cell">' +  content + '</div>';
        },
        renderer: columnrenderer
      },
      {
        text: 'Current Reading',
        width: 250,
        cellsrenderer: (row, column, value): string => {
          var content = this.filteredUtilityWaterListData.records[row].waterCharge.length > 0 ? this.filteredUtilityWaterListData.records[row].waterCharge[0]["currReading"] : ''
          return '<div class="jqx-custom-inner-cell">' +  content + '</div>';
        },
        renderer: columnrenderer
      }
    ]

    this.utilityInsuranceHeader = [{
      text: 'Insurance Number',
      datafield: 'insuranceNumber',
      width: 200,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Condo Unit Count',
      datafield: 'apartmentUnitCount',
      width: 250,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Total Cost',
      datafield: 'totalCost',
      width: 250,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Cost Per Condo',
      datafield: 'costPerAptUnit',
      width: 250,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Date Entered',
      datafield: 'dateEntered',
      width: 250,
      cellsrenderer: (row, column, value): string => {
        return '<div class="jqx-custom-inner-cell">' + moment(value).format(this.timeZone.date) + '</div>'
      },
      renderer: columnrenderer
    }
    ]

    let params = {
      apartmentId: this.sessionService.apartmentId
    }
    this.utilityTrackerService.getUtilityTrackerCategorysByApartmentId(params).subscribe((res: any) => {
      this.utilityCategoryData = res.filter(item => {
        return item.isActive;
      });
      this.isUtilityCategoryLoaded = true;
    },
      error => {
      });
    this.apartmentService.getApartmentBlockByApartmentId(params).subscribe((res: any) => {
      this.propertyTowerData = res;
    });
  }



}
