import { Component, OnInit, Injector, ViewChild } from '@angular/core';
import { UtilityTrackerService } from 'src/app/api/controllers/UtilityTracker';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';
import { ModalService } from 'src/app/shared/services/modal.service';

import * as _ from 'underscore';
import * as moment from 'moment';
import _moment from "moment-timezone";

import { MatDrawer } from '@angular/material/sidenav';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-utility-setup',
  templateUrl: './utility-setup.component.html',
  styleUrls: ['./utility-setup.component.scss']
})
export class UtilitySetupComponent implements OnInit {

  utilityCategoryData: any;
  apartment_id: number;

  isUtilityCategoryLoaded: boolean = false;
  isUtilityCategorySubmitted: boolean = true;

  isUtilityCategoryNew: boolean = false;
  isUtilityCategoryUpdate: boolean = false;

  selectedItem: any;

  UtilityCategory: string = "";
  UtilityRateUnit: number;
  UtilityCategoryUpdateId: number = 0;

  alertMessage: string = "";
  isError: boolean = false;

  utilityListData: any;

  modalService: any;

  public categorySearch: string = '';
  public isEdit: boolean = false;
  @ViewChild('utility', { static: true })
  matDrawer: MatDrawer;
  apiSubscribe:Subscription;
  public params = {
    apartmentId: this.sessionService.apartmentId
  }
  constructor(
    private injector: Injector,
    private utilityTrackerService: UtilityTrackerService,
    private lookupService: LookupService,
    private sharedService: SharedService,
    private sessionService: SessionService
  ) {
    this.modalService = this.injector.get(ModalService);
  }

  addNewUtilityCategory() {
    this.UtilityCategory = "";
    this.UtilityRateUnit = undefined;
    this.isUtilityCategoryNew = true;
    this.isUtilityCategoryUpdate = false;
    this.matDrawer.toggle();
    this.isEdit = false;
  }

  updateUtilityCategory(item) {
    this.isUtilityCategoryUpdate = true;
    this.isUtilityCategoryNew = false;
    this.selectedItem = item;
    this.UtilityCategory = item.category;
    this.UtilityRateUnit = item.ratePerUnit;
    this.UtilityCategoryUpdateId = item.utilityTrackerCategoryId;
    this.isEdit = true;
    this.matDrawer.toggle();
  }

  deleteUtilityCategory(item) {

    this.isUtilityCategoryLoaded = false;
    var params = {
      utilityId: item.utilityTrackerCategoryId,
      deleteBy: parseInt(this.sessionService.userId)
    }

    this.utilityTrackerService.deleteUtilityTrackerCategory(params).subscribe((res: any) => {
      this.sharedService.setUnitListDeleteIndex(null);
      this.loadUtilityCategory()
    });
  }

  showConfirmModal(form) {
    if (this.isUtilityCategoryNew) {
      //  this.modalService.showCategoryModal(form);
      this.submitUtilityCategoryForm(form)
    } else {
      this.addOrUpdateUtilityCategoryForm(form)
    }
  }

  showRemoveCategoryConfirmModal(item) {
    this.modalService.showConfirmModal(item);
  }

  removeCategoryBox() {
    this.isUtilityCategoryNew = false;
    this.isUtilityCategoryUpdate = false;
    this.isUtilityCategorySubmitted = true;
    this.isError = false;
  }

  getUtilitysCount(id) {

    var data = this.utilityListData.filter(item => {
      return item.utilityCategoryId == id;
    });

    if (data === undefined || data.length === 0) {
      return 0
    }
    else {
      return data.length
    }

  }

  submitUtilityCategoryForm(form) {
    //adding new category not supporting  now..
    this.isError = true;

    this.alertMessage = "Please raise Service Ticket"
  }

  addOrUpdateUtilityCategoryForm(form) {

    this.isUtilityCategorySubmitted = false;
    this.isError = false;

    if (this.isUtilityCategoryNew) {

      let details = {

        "apartmentId": this.apartment_id,
        "category": this.UtilityCategory,
        "ratePerUnit": this.UtilityRateUnit,
        "comments": "",
        "isActive": true,
        "insertedBy": parseInt(this.sessionService.userId),
        "insertedOn": _moment().toISOString(),
        "updatedBy": null,
        "updatedOn": null,

      }

      let params = {
        utilityTrackerCategory: details
      }

      this.utilityTrackerService.addUtilityTrackerCategory(params).subscribe((res: any) => {

        if (res.message) {
          let params = {
            apartmentId: this.sessionService.apartmentId
          }
          this.utilityTrackerService.getUtilityTrackerCategorysByApartmentId(params).subscribe((res: any) => {
            this.isUtilityCategorySubmitted = true;
            this.sharedService.openSnackBar("Category added Successfully!", 'success');
            this.matDrawer.toggle();
            this.utilityCategoryData = res.filter(item => {
              return item.isActive;
            });

          });
        }
        else {
          this.isUtilityCategorySubmitted = true;
          this.isError = true;
          this.alertMessage = res.errorMessage;
        }
      });
    }
    else {

      let details = {

        "utilityTrackerCategoryId": this.UtilityCategoryUpdateId,
        "apartmentId": this.sessionService.apartmentId,
        "category": this.UtilityCategory,
        "ratePerUnit": this.UtilityRateUnit,
        "comments": "",
        "isActive": true,
        "insertedBy": this.selectedItem.insertedBy,
        "insertedOn": this.selectedItem.insertedOn,
        "updatedBy": parseInt(this.sessionService.userId),
        "updatedOn": _moment().toISOString(),

      }
      let params = {
        utilityTrackerCategory: details
      }

      this.utilityTrackerService.updateUtilityTrackerCategory(params).subscribe((res: any) => {

        if (res.message) {
          let params = {
            apartmentId: this.sessionService.apartmentId
          }
          this.utilityTrackerService.getUtilityTrackerCategorysByApartmentId(params).subscribe((res: any) => {
            this.isUtilityCategorySubmitted = true;
            this.sharedService.openSnackBar("Category updated Successfully!", 'success');
            this.matDrawer.toggle();
            this.utilityCategoryData = res.filter(item => {
              return item.isActive;
            });
            this.isUtilityCategoryNew = false;
            this.isUtilityCategoryUpdate = false;

          });
        }
        else {
          this.isUtilityCategorySubmitted = true;
          this.isError = true;
          this.alertMessage = res.errorMessage;
        }
      });
    }
  }

  loadUtilityCategory() {
    let params = {
      apartmentId: this.sessionService.apartmentId
    }
    this.utilityTrackerService.getUtilityTrackerCategorysByApartmentId(params).subscribe((res: any) => {
      this.utilityCategoryData = res.filter(item => {
        return item.isActive;
      });
      this.isUtilityCategoryLoaded = true;
    },
      error => {
      });
  }

  ngOnInit() {
    this.apartment_id = this.sessionService.apartmentId
    this.loadUtilityCategory()

    this.sharedService.newcategoryadd.subscribe(is_category_form => {

      if (is_category_form) {
        this.submitUtilityCategoryForm(is_category_form);
      }
    })

    this.apiSubscribe = this.sharedService.unitlistdeleteindexcast.subscribe(item_id => {

      if (item_id) {
        this.deleteUtilityCategory(item_id);
      }
    })
  }

  ngOnDestroy(){
    this.apiSubscribe.unsubscribe();
  }

}
