import { Component, OnInit,Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { UtilityTrackerService } from 'src/app/api/controllers/UtilityTracker';
import { ApartmentService } from 'src/app/api/controllers/Apartment';

import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';

import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-add-insurance',
  templateUrl: './add-insurance.component.html',
  styleUrls: ['./add-insurance.component.scss']
})
export class AddInsuranceComponent implements OnInit {

  @Output() onCancel = new EventEmitter();

  insuranceCharge: any;

  isInsuranceSubmitted: boolean = true;
  isEdit: boolean = false;
  isError: boolean = false;
  categoryCostPerApt: any;
  alertMessage:string = "";

  timeZone:any;

  public params = {
    apartmentId: this.sessionService.apartmentId
  }
  constructor(  private router: Router,
    private utilityTrackerService:UtilityTrackerService,
    private apartmentService: ApartmentService,
    private sharedService:SharedService,
    private sessionService:SessionService) { 

    }


  Cancel(){
    this.onCancel.emit(false)
  }

  submitInsuranceChargeForm(form) {

    this.isInsuranceSubmitted = false;

      let details = {

        "apartmentId": this.sessionService.apartmentId,
        "apartmentUnitCount": this.insuranceCharge.AptunitCount,
        "insuranceNumber": this.insuranceCharge.InsuranceNo,
        "totalCost": this.insuranceCharge.TotalCost,
        "costPerAptUnit": this.insuranceCharge.CostPerApt,
        "dateEntered": moment(this.insuranceCharge.DateEntered).utcOffset(this.timeZone.offset).format(),
        "comments": this.insuranceCharge.Comments,
        "isActive": true,
        "insertedBy": parseInt(this.sessionService.userId),
        "insertedOn": _moment().toISOString(),
        "updatedBy": null,
        "updatedOn": null

      }
      let params ={
        utilityTrackerInsuranceCharge: details
      }

      this.utilityTrackerService.addUtilityTrackerInsuranceCharge(params).subscribe((res:any) => {

          if(res.message) {
            this.isInsuranceSubmitted = true;
            this.resetData();
            this.sharedService.openSnackBar("Insurance added successfully",'success');
            this.router.navigateByUrl('/ams/utility/history')
          }
          else {
            this.isInsuranceSubmitted = true;
            this.isError = true;
            this.alertMessage = res.errorMessage;
          }
        }, error => {
          this.isInsuranceSubmitted = true;
          this.isError = true;
          this.alertMessage = "Some error occured";
      });

  }

  calculateCostPerAptFrmTotalCost(e){
    if(this.insuranceCharge.TotalCost){
      let result_des = this.insuranceCharge.TotalCost/this.insuranceCharge.AptunitCount;
     this.insuranceCharge.CostPerApt = result_des.toFixed(2);
    }
  }

  resetData(){
    this.insuranceCharge.InsuranceNo = undefined;
    this.insuranceCharge.TotalCost = undefined;
    this.insuranceCharge.CostPerApt = undefined;
    this.insuranceCharge.Comments = undefined;
  }

  ngOnInit(): void {

    this.sharedService.timezonecast.subscribe( timeZone => this.timeZone = timeZone )

    this.insuranceCharge = {};

    this.insuranceCharge.DateEntered = _moment().toISOString();

    let params = {
      apartmentId: this.sessionService.apartmentId
    }
    this.apartmentService.getApartmentBlockUnitByApartmentId(params).subscribe((res:any) =>{
      this.insuranceCharge.AptunitCount = res.length;
    })

   }


}
