import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { SharedService } from 'src/app/shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { UtilityTrackerService } from 'src/app/api/controllers/UtilityTracker';
import { SessionService } from 'src/app/core/session/session.service';

import * as _ from 'underscore';
import * as moment from 'moment';
import _moment from "moment-timezone";
import { Subject } from 'rxjs';
import { debounceTime } from 'rxjs/operators';

@Component({
  selector: 'app-unit-reading-info',
  templateUrl: './unit-reading-info.component.html',
  styleUrls: ['./unit-reading-info.component.scss']
})
export class UnitReadingInfoComponent implements OnInit {

  @Input() tower: any;
  @Input() unit: any;
  @Input() waterReadingPeriodID: any;
  @Input() utility_selected_category: any;
  @Output() refreshEmitList = new EventEmitter();
  private calculateSubject: Subject<string> = new Subject();

  unitTypeData: any;
  unitTypeId: any;

  categoryCostPerApt: any;
  waterReading: any;
  isError: boolean = false;
  alertMessage: string = "";

  isEdit: boolean = false;

  timeZone:any;

  constructor(
    private apartmentService: ApartmentService,
    private lookupService: LookupService,
    private utilityTrackerService: UtilityTrackerService,
    private sharedService: SharedService,
    private sessionService: SessionService
  ) { }

  showEditForm() {
    this.isEdit = !this.isEdit;
  }

  getBooleanStatus(status) {
    return status ? 'Yes' : 'No'
  }

  getUnitType(name) {

    var data = _.filter(this.unitTypeData, function (item) {
      if (item.lookupValueName === name)
        return item;
    });

    if (data === undefined || data.length == 0) {
      this.unitTypeId = "";
    }
    else {
      this.unitTypeId = data[0].lookupValueId;
    }
  }


  submitWaterMeterReading(form) {

    let details = {

      "apartmentId": this.sessionService.apartmentId,
      "apartmentBlockId": this.tower.apartmentBlockId,
      "apartmentBlockUnitId": this.unit.apartmentBlockUnitId,
      "waterChargeTypeId": null,
      "utilityTrackerWaterChargePeriodId": this.waterReadingPeriodID,
      "prevReading": this.waterReading.prevReading,
      "currReading": this.waterReading.currReading,
      "units": this.waterReading.units,
      "unitNumber": null,
      "cost": parseInt(this.waterReading.cost),
      "dateTaken": moment(this.waterReading.dateTaken).utcOffset(this.timeZone.offset).format(),
      "comments": this.waterReading.comments,
      "isActive": true,
      "insertedBy": parseInt(this.sessionService.userId),
      "insertedOn": _moment().toISOString(),
      "updatedBy": null,
      "updatedOn": null
    }

    let params = {
      utilityTrackerWaterCharge: details
    }

    this.utilityTrackerService.addUtilityTrackerWaterCharge(params).subscribe((res: any) => {

      if (res.message) {
        this.isEdit = false;
        this.sharedService.openSnackBar("Water reading added successfully",'success');
        this.refreshEmitList.emit();
      }
      else {
        this.isError = true;
        this.alertMessage = res.errorMessage;
      }
    }, error => {
      this.isError = true;
      this.alertMessage = "Some error occured";
    });
  }

  calculateUnitPerAptFrmCurReading() {
    if (this.waterReading.currReading && this.waterReading.prevReading) {
      if (this.waterReading.currReading > this.waterReading.prevReading) {
        this.waterReading.units = (this.waterReading.currReading - this.waterReading.prevReading);
        this.waterReading.cost = (this.waterReading.units * this.utility_selected_category.ratePerUnit).toFixed(2);
      } else {
        this.sharedService.openSnackBar('Current Reading Should Greater than Previous Reading !', 'error')
      }
    }
  }

  calculateUnitPerAptFrmPreReading(e) {
    if (this.waterReading.currReading && this.waterReading.prevReading) {
      this.waterReading.units = (this.waterReading.currReading - this.waterReading.prevReading);
      this.waterReading.cost = (this.waterReading.units * this.utility_selected_category.ratePerUnit).toFixed(2);
    }
  }


  ngOnInit() {

    this.sharedService.timezonecast.subscribe( timeZone => this.timeZone = timeZone )

    this.waterReading = {};
    //let apartment_id = this.sessionService.apartmentId;
    if (this.unit.water_charge == undefined) {
      this.unit.water_charge = {};
    }
    if (this.unit.water_charge.prevReading && this.unit.water_charge.currReading) {
      this.isEdit = false;
      this.waterReading.units = (this.unit.water_charge.currReading - this.unit.water_charge.prevReading);
    } else {
      this.isEdit = true;
    }
    this.waterReading.prevReading = this.unit.water_charge.prevReading;
    this.waterReading.currReading = this.unit.water_charge.currReading;
    this.waterReading.dateTaken = _moment().toISOString();

    this.unitTypeId = "";

    //get unit type
    let params = {
      LookupTypeId: 1
    }
    this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res: any) => {
      this.unitTypeData = res;

      setTimeout(() => {
        this.getUnitType(this.unit.unitType);
      }, 1000);

    },
      error => {

      });

    this.calculateSubject.pipe(
      debounceTime(500)
    ).subscribe(searchTextValue => {
      this.calculateUnitPerAptFrmCurReading();
    });

  }

}
