import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { UtilityReportsComponent } from './components/utility-reports/utility-reports.component';
import { ElectricityReportComponent } from './components/utility-reports/electricity-report/electricity-report.component';
import { InsuranceReportComponent } from './components/utility-reports/insurance-report/insurance-report.component';
import { WaterReportComponent } from './components/utility-reports/water-report/water-report.component';
import { UtilitySetupComponent } from './components/utility-setup/utility-setup.component';
import { UtilityAddReadingComponent } from './components/utility-add-reading/utility-add-reading.component';
import { ElectricityReadingComponent } from './components/utility-add-reading/electricity-reading/electricity-reading.component';
import { WaterReadingComponent } from './components/utility-add-reading/water-reading/water-reading.component';
import { UtilityHistoryComponent } from './components/utility-history/utility-history.component';
import { UtilityReportDataComponent } from './components/utility-reports/utility-report-data/utility-report-data.component';
import { AddInsuranceComponent } from './components/utility-add-reading/add-insurance/add-insurance.component';

const routes: Routes = [

	{ path: '', redirectTo: 'settings', pathMatch: 'full' },
	{ path: 'settings', component: UtilitySetupComponent, canActivate: [AuthGuard] },
	{
		path: 'reading', component: UtilityAddReadingComponent, canActivate: [AuthGuard],
		children: [
			{ path: '', redirectTo: 'water-reading', pathMatch: 'full' },
			{ path: 'water-reading', component: WaterReadingComponent, canActivate: [AuthGuard] },
			{ path: 'electricity-reading', component: ElectricityReadingComponent, canActivate: [AuthGuard] },
			{ path: 'insurance', component: AddInsuranceComponent, canActivate: [AuthGuard] },
			{ path: '**', redirectTo: 'water-reading', pathMatch: 'full' }
		]
	},
	{ path: 'history', component: UtilityHistoryComponent, canActivate: [AuthGuard] },
	{ path: 'reports', component: UtilityReportsComponent, canActivate: [AuthGuard] },
	{ path: 'reports/:name/:id', component: UtilityReportDataComponent, canActivate: [AuthGuard] },
	{ path: 'reports/electricity', component: ElectricityReportComponent, canActivate: [AuthGuard] },
	{ path: 'reports/water', component: WaterReportComponent, canActivate: [AuthGuard] },
	{ path: 'reports/insurance', component: InsuranceReportComponent, canActivate: [AuthGuard] },
	{ path: '**', redirectTo: 'settings', pathMatch: 'full' }

];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class UtilityRoutingModule { }
